import React, { useState, useEffect, useRef } from 'react';
import { VectorMap } from 'react-jvectormap';
import CachedIcon from '@material-ui/icons/Cached';
import { country_code, CountryCode } from './mockedCountryCodes';
import { useTranslation } from 'react-i18next';

export type DashboardMapViewType = {
	onlineClients: DashboardMapOnlineClientsType[];
	reloadMapRequest: () => void;
};
type DashboardMapOnlineClientsType = {
	first_name: string;
	last_name: string;
	country_code: string;
	latitude: string;
	longitude: string;
	is_staff: boolean;
};

type OnlineUsersByRegionType = {
	[key: string]: {
		count: number;
		onlineUsers: DashboardMapOnlineClientsType[];
		latLng: number[];
		style: Record<string, any>;
		countryCode: string;
	};
};

const DashboardMapView = ({ onlineClients, reloadMapRequest }: DashboardMapViewType) => {
	const [clients, setClients] = useState<any[]>([]);
	const { t } = useTranslation();

	const handleMouseMove = () => {
		const jvectormaps: any = document.getElementsByClassName('jvectormap-tip');
		const length = jvectormaps.length;
		if (length > 0) {
			for (let i = 0; i < length - 1; i++) {
				if (!!jvectormaps[i] && !!jvectormaps[i].innerHTML) {
					jvectormaps[i]?.parentNode.removeChild(jvectormaps[i]);
				}
			}
		}
	};

	useEffect(() => {
		if (onlineClients) {
			const calculatedOnlineUsersByRegion = onlineClients.reduce<OnlineUsersByRegionType>(
				(prev, current) => {
					if (prev[current.country_code]) {
						return {
							...prev,
							[current.country_code]: {
								...prev[current.country_code],
								latLng: [+current.latitude, +current.longitude],
								onlineUsers: [...prev[current.country_code].onlineUsers, current],
								count: prev[current.country_code].count + 1
							}
						};
					} else {
						return {
							...prev,
							[current.country_code]: {
								latLng: [+current.latitude, +current.longitude],
								onlineUsers: [current],
								style: { fill: `${current.is_staff ? '#34a9f7' : 'rgb(0, 208, 189)'}` },
								count: 1,
								countryCode: current.country_code
							}
						};
					}
				},
				{}
			);

			setClients(Object.values(calculatedOnlineUsersByRegion));
		}
	}, [onlineClients]);

	return (
		<div
			className="h-100 w-100 rounded-10 bg-kgrey-bg p-3 dark:bg-kdark-lighter"
			onMouseMove={handleMouseMove}
			onMouseOver={handleMouseMove}
		>
			<div className="h-100 w-100 ">
				<div className="mb-3 flex items-center justify-between">
					<div className="flex items-center dark:text-white">
						<span className="text-base font-medium text-primary-dark">
							{onlineClients.length ? onlineClients.length : null}
						</span>
						<span className="mx-2 text-base font-medium"> {t('online_users')} </span>
					</div>
					<div onClick={reloadMapRequest} className="contextual-link cursor-pointer">
						<CachedIcon fontSize="small" className="inline-block h-4 w-4 dark:text-primary" />
					</div>
				</div>
				<div className="w-100 overflow-hidden rounded-10" style={{ height: 'calc(100% - 50px)' }}>
					<VectorMap
						map="world_mill"
						backgroundColor={'white'}
						containerStyle={{
							width: '100%',
							height: '300px'
						}}
						regionStyle={{
							initial: {
								fill: '#DEE4E8'
							}
						}}
						zoomButtons
						zoomOnScroll={false}
						containerClassName="map"
						markers={clients}
						markerLabelStyle={{
							initial: {
								color: '#3e484f',
								'font-size': 8
							}
						}}
						markerStyle={{
							initial: {
								stroke: 'none'
							}
						}}
						labels={{
							markers: {
								render: function (index: number) {
									return clients?.[index]?.onlineUsers?.length;
								}
							}
						}}
						series={{
							markers: [
								{
									attribute: 'r',
									scale: [9, 15],
									values: clients.map((user) => user?.count),
									min: 1,
									max: onlineClients?.length * 2 ?? 50
								}
							]
						}}
						onMarkerTipShow={(event: any, label: any, index: string) => {
							label.html(
								`<div> 
									<b>Online Users: </b> ${clients?.[+index]?.onlineUsers?.map(
										(user: DashboardMapOnlineClientsType) =>
											` ${user?.first_name} ${user?.last_name}`
									)}
									<br/>
									<b>Country:</b> ${country_code?.[clients[+index]?.countryCode]?.name}
									<br/>
									<b>Count: </b>
									${clients[+index]?.onlineUsers?.length}
								</div>
								`
							);
						}}
					/>
				</div>
			</div>
		</div>
	);
};

export default DashboardMapView;
